* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    padding: 50px;
    background-color: rgb(173, 173, 173);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100vh;
    border: 2px solid black;
}

.mainbox {
    width: 20rem;
    min-height: 20rem;
    border-radius: 20px;
    background: rgb(140, 219, 140);
    box-shadow: 2px 1px 20px 2px rgba(0, 0, 0, 0.416);
}

.mainbox h2,
h1 {
    text-align: center;
    padding: 10px;
    color: rgb(173, 220, 160);
    background-color: rgb(28, 197, 22);
}

.inputDiv {
    display: flex;
    justify-content: center;
}

.mainbox input {
    background-color: transparent;
    border: 0;
    width: 15rem;
    font-size: large;
    color: rgb(248, 255, 151);
    border-bottom: 2px solid green;
    outline: none;
}

.mainbox input:placeholder-shown {
    text-align: center;
}

.todoWork {
    /* border: 2px solid red; */
    margin: 10px;
}

.todoWork i:hover {
    color: red;
    cursor: pointer;
    /* transform: scale(2); */
}

.todoWork i {
    font-size: 1.3rem;
    margin: 1px;
}

.todoWork li {
    list-style: none;
    /* list-style- : inside; */
    margin: 10px;
    color: darkgreen;
    font-size: larger;
    font-weight: 500;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    /* border: 2px solid red; */
}

.btn2 {
    /* margin:  px; */
    width: 2rem;
    height: 2rem;
    /* padding: 5px; */
    font-size: larger;
    font-weight: bolder;
    color: aliceblue;
    background-color: rgb(207, 111, 47);
    /* border: 2px solid rgb(207, 111, 47); */
    cursor: pointer;
    border-radius: 50%;
}

.btn {
    width: 100px;
    background-color: transparent;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn2:hover {
    background-color: green;
}

@media screen and (max-width: 480px) {
    .container {
        font-size: 0.8rem;
        background-color: rgb(171, 184, 173);
    }
    .mainbox input:placeholder-shown {
        font-size: 0.8rem;
    }
    .mainbox input {
        width: 10rem;
        margin-right: 10px;
    }
}